:global(.bm-burger-button){
    position:fixed;
    width:36px;
    height:36px;
    left:32px;
    top:32px;
}


:global(.bm-burger-bars){
    /* background:#373a47; */
    background:rgb(223,223,223);
}

:global(.bm-burger-bars-hover){
    background:#000;
}

:global(.bm-cross-button){
    height:24px;
    width:24px;
}

/* Colour of cross button */
:global(.bm-cross){
    background-color: #fff;
}
:global(.bm-menu-wrap){
    position:fixed;
    height:100vh;
    overflow-x:hidden;
    box-shadow:0px 4px 31px rgba(0,0,0,0.15);
}

:global(.bm-item-list){
    padding:0.8em;
}

:global(.bm-menu){
    /* background: linear-gradient(39.97deg, #03d6f3 0.79%, #458ff6 79.29%); */
    background:linear-gradient(150deg, #84a0ad95 30%,#ffffff );
    padding: 2.5em 1.5em 0 0;
    height:100vh;
    overflow:hidden !important;
    /* opacity: 50%; */
    

}



.menu-item{
    position:relative;
    font-size:2em;
    text-decoration:none;
    color:#fff;
    font-family:Montserrat;
    font-style:normal;
    font-weight:normal;
    padding:10px;
}


.menu-item:before{
    content:'';
    position:absolute;
    width:100%;
    height:0;
    bottom:0;
    left:0;
    background-color: #fff;
    visibility: hidden;
    transition: all 0.1s ease-in-out;
}


.active-item{
    font-weight:500;
}

.menu-item:hover:before,
.active-item:before{
    visibility:visible;
    height:3px;
}

