
body{
    margin:0;
    min-height:100vh;
    box-sizing:border-box;
    width:100%;
    overflow-x:hidden;
    font-family: 'Poppins', sans-serif;
}

input:focus,
textarea:focus,
select:focus{
    outline:none;
}

.section-container{
    box-sizing: border-box;
    min-height: 100vh;
    position: relative;
    padding: 50px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    background-color: rgb(54,54,54);
    /* background-color: #FFFACD; */
}

@media only screen  and (max-width:640px){
    .section-container{
        padding:100px 0;
    }
}